<template>
<div class="overflow-auto">
		
	<!-- <div class="text-left"> -->
		
		<b-table class="border-bottom"
			id="my-talbe"
			responsive
			striped 
			hover 
			caption-top 
			sticky-header="800px"
			no-border-collapse
			:head-row-variant="themeClr"
			:fields="fields.show"			
			:items="lists"
			:busy="isBusy"
			:per-page="perPage"
			:current-page="currentPage"
			small
		>
			<template v-slot:table-caption >
				<h4 class="text-center align-text-bottom" >
					<span :class="`text-${themeClr}`">{{title}}&nbsp;--&nbsp;</span>
					<span 
						v-show="subTitle"
						:class="`badge badge-${themeClr}`" 
						style="font-size:16px;"
					
					>
						{{subTitle}}
					</span>
					
				</h4>
				
				<!-- modal, '' -->
				<b-button 
					v-if="actNav.routeStr!='system'"
					size="sm" 
					@click="getInfo('', '', $event.target)" 
					class="py-0 px-1 mr-1 float-right"
					variant="primary"
					
				>
					
				</b-button>
			</template>
			
			<!-- <template v-slot:head()="data"> -->
				<!-- <span :class="`text-${themeClr}`">{{ data.label }}</span> -->
			<!-- </template> -->
			
			<template v-slot:table-busy>
				<div class="text-center text-muted my-2">
					<b-spinner class="align-middle"></b-spinner>
					<strong> ...</strong>
				</div>
			</template>
			
			<template v-slot:cell(topic)="data">
				<!-- <b-link href="#">{{ data.value }}</b-link> -->
				
				<b-button 
					variant="link" 
					v-on:click="getInfo(data.item, data.index, $event.target)" 
					class="mr-1 text-left"
				>
					{{ data.value }}
				</b-button>
			</template>
			
			<template v-slot:cell(name)="data">
				<b-button 
					variant="link" 
					v-on:click="getInfo(data.item, data.index, $event.target)" 
					class="mr-1">
					{{ data.value }}
				</b-button>
			</template>
			<!-- actions -->
			<template v-slot:cell(actions)="row">
				<!-- modal class="mr-1"-->
				<b-button 
					size='sm'
					variant="primary"
					class="py-0 px-1 ml-1 mt-1"					
					@click="getInfo(row.item, row.index, $event.target)" 
				>
					
				</b-button>
				
				<!-- /detail-->
				<b-button 
					size='sm'
					variant="primary"
					class="py-0 px-1 ml-1 mt-1"
					:class="row.detailsShowing ? 'btn-'+themeClr: null"
					@click="row.toggleDetails"
				>
					{{ row.detailsShowing ? '': ''  }} 
				</b-button>
			
			</template>

			<!-- detail -->
			<template v-slot:row-details="row">
				<b-card>
					<b-media>
						<template 
							v-slot:aside
							v-if="actNav.routeStr=='mentor'"
						>
							<b-img 
								blank 
								blank-color="#ccc" 
								width="168" 
								height="200"
								alt="placeholder"
								class="mr-3 rounded shadow"
							>
							</b-img>
						</template>
					
						<b-row v-for="(obj, index) in fields.hide" :key="index">
							<template v-if="obj.formType">
							<b-col 
								cols="3" 
								class="text-right"> 
								<strong>{{ obj.label+''}}</strong>
							</b-col>
							<b-col 
								class="text-left border-bottom"
							>
								{{ row.item[obj.key] }}
							</b-col>
							</template>
						</b-row>
					</b-media>
				</b-card>
			</template>
			
		</b-table>
	<!-- </div>	 -->
	<div class="text-right" v-show="total > perPage">
		<b-row>
			<b-col cols="8" style="padding-top:5px;">
				<span ><b-badge :variant="themeClr" style="font-size:14px;">{{ total }}</b-badge></span>
			</b-col>
			<b-col>
				<b-form-group
					label-cols="6"
					label-align="right"
					label=":"
				>
					<!-- size="sm"  -->
					<b-form-select 
						v-model="perPage" 
						:options="options"
					></b-form-select>	
				</b-form-group>			
			</b-col>
		</b-row>
			
		<b-row>
			<b-col>
				<!-- size="sm" -->
				<b-pagination
					aria-controls="my-table"
					align="right"
					v-model="currentPage"
					:total-rows="total"
					:per-page="perPage"	
				></b-pagination>
			</b-col>	
		</b-row>
		
	</div>
	
	<!-- Info modal -->
	<b-modal 
		size="lg"
		:id="infoModal.id" 
		:title="infoModal.title" 
		@hide="resetInfoModal"
		ok-only 
	>
		<pre>{{ infoModal.content }}</pre>
	</b-modal>
	
</div>
</template>

<script>
import { mapState,mapGetters, mapActions } from 'vuex'

//import { asyGetBsvComponent as aGetBsv} from '@/components/util-bootstrap-vue'


//BsVcomponent
import { 
	BTable,
	BModal,
	BPagination,
	BCard,
	BButton,
	BBadge,
	BRow,
	BCol,
	BFormGroup,
	//BFormCheckbox,
	BFormSelect, 
	BMedia,
	BImg
} from 'bootstrap-vue'

import {fieldProps,FIELDS} from '@/components/util-the-table'

//native JS Module export
export default {
    name: 'TheTable',
	props: {
		lang:{
			type:String,
			default:'en',
		}
	},
    data:function() {
		return {
			
			isBusy:false,
			perPage: 10,
			currentPage: 1,
			lists:[],
			options: [
				{ value: 5, text: '5' },
				{ value: 10, text: '10' },
				{ value: 20, text: '20' },
				{ value: 50, text: '50', }
			],
			infoModal: {
				id: 'info-modal',
				title: '',
				content: ''
			}
		}
	},
	computed: {
		title:function(){
			return this.actNav.label
		},
		subTitle:function(){
			return this.actItem.label
		},
		//
		themeClr:function(){
			return this.actNav.themeClr
		},
		//	
		total:function(){
			return this.resLists.length
		},
		fields:function(){
			return this.setFields()
		},
		...mapGetters({
			actNav:'actNavbar',
			actItem:'actEntry'
		}),
		...mapState({
			//
			resLists:state => state.fetchCont.response.lists,
		}),
	},
	watch:{
		resLists:function(){
			this.isBusy=true
			//lists
			this.setLists()
			//fields
			this.setFields()
			
			this.isBusy=false
			return 
		}
	},
	methods: {
		setLists(){
			let self=this
			let resLists=self.resLists
			let lists=self.lists=[]
			//snTheTable
			if(resLists.length){
				resLists.forEach((list,idx)=>{
				//listObject.assign()
					if(typeof list =='object'){
						lists[idx]=Object.assign({},{'serial-number':idx*1+1},list)
					}
				})
			}
			return 
		},
		setFields(){
			let self=this
			let route=self.actItem.routeStr
			let fields=FIELDS.hasOwnProperty(route)
						?FIELDS[route]:[]
			
			let lang=self.lang
			let showArr=[]
			let hideArr=[]
			
			if(fields.length){
				fields.forEach(field=>{
					let label=field.label.hasOwnProperty(lang)
								?field.label[lang]
								:field.label.en
					let obj=Object.assign({},fieldProps,field,{'label':label})
					//fields.show
					field.isShown?showArr.push(obj):hideArr.push(obj)
				})
			}
			return {'show':showArr,'hide':hideArr}
		},
		getInfo(item, index, button) {
			this.infoModal.title = `Row index: ${index}`
			//this.infoModal.content = JSON.stringify(item, null, 2)
			this.infoModal.content =item
			this.$root.$emit('bv::show::modal', this.infoModal.id, button)
		},
		resetInfoModal() {
			this.infoModal.title = ''
			this.infoModal.content = ''
		},
		...mapActions({
			//getTableItemsBy: 'asyChangeTable'
		}),
	},
	components:{
		
		//BSVcomponent
		'b-table':BTable,
		'b-modal':BModal,
		'b-pagination':BPagination,
		'b-card':BCard,
		'b-button':BButton,
		'b-form-group':BFormGroup,
		'b-row':BRow,
		'b-col':BCol,
		//'b-form-checkbox':BFormCheckbox,
		'b-form-select':BFormSelect,
		'b-badge':BBadge,
		BMedia,
		BImg
		
		/*
		//
		'b-table':aGetBsv('b-table'),
		'b-modal':aGetBsv('b-modal'),
		'b-pagination':aGetBsv('b-pagination'),
		'b-card':aGetBsv('b-card'),
		'b-button':aGetBsv('b-button'),
		'b-form-checkbox':aGetBsv('b-form-checkbox'),
		'b-form-select':aGetBsv('b-form-select'),
		'b-badge':aGetBsv('b-badge'),
		*/
	},
	mounted(){
		
		//return this.lists=this.resLists.slice()
	}

}

</script>

